<template>
  <ul class="sidenav app-sidenav" :class="{ open: value }">
    <router-link
      v-for="link in links"
      v-bind:key="link.url"
      :to="link.url"
      custom
      v-slot="{ navigate, isActive, isExactActive, href }"
    >
      <li
        @click="navigate"
        :class="(isActive && 'active', isExactActive && 'active')"
      >
        <a :href="href" class="waves-effect waves-red pointer">{{
          link.title
        }}</a>
      </li>
    </router-link>
  </ul>
</template>

<script>
export default {
  props: ["value"],
  data: () => ({
    links: [
      { title: "Отрядная статистика", url: "/" },
      { title: "Общая статистика игроков", url: "/players" },
      { title: "Общая статистика отрядов", url: "/squads" },
    ],
  }),
};
</script>
